

















import {Component, Prop, Vue, Watch} from "vue-property-decorator";
import searchPng from "@/assets/img/search.png";
import $ from "jquery";

@Component({
    data: () => ({
        searchPng
    })
})
export default class Search extends Vue {
    showInput = false


    @Prop({
        type: Boolean,
        default: () => false
    })
    alwaysOpen!: boolean

    @Prop({
        type: Boolean,
        default: () => false
    })
    fullSize!: boolean

    @Watch('showInput')
    watchShowInput(val: boolean): void {
        if (this.alwaysOpen) return

        const $input = $(this.$refs['input'])
        const $label = $(this.$refs['placeholder'])

        if (val) {
            $label.animate({
                width: 0
            }, {
                duration: 'fast',
                complete() {
                    $input.animate({
                        width: 200
                    }, {
                        duration: 'fast',
                        done() {
                            $input.trigger('focus')
                        }
                    })
                }
            })

        } else {
            $input.animate({
                width: 0
            }, {
                duration: 'fast',
                complete: () => {
                    $label.animate({
                        width: 60
                    })
                }
            })
        }
    }
}
